<template>
  <div class="DashboardPrototipo row pr-2">
    <!-- Titulo -->
    <div
      class="div-titulo col-12 d-flex center-content-between flex-wrap flex-md-nowrap p-0 mb-0"
      style="height:38px;"
    >
      <div class="form-inline col-12 pl-0 mb-1 pr-1">
        <h1 class="titulo col-xl-2 col-md-2 col-sm-2 col-3 px-0 pr-1">Tabela</h1>

        <div
          class="form-group col-xl-10 col-md-10 col-sm-10 col-9 mb-0 p-0"
          style="justify-content: flex-end!important;"
        >
          <div class="input-group mr-3 ml-auto mb-0 mt-0 p-0">
            <select class="form-control form-control-sm" v-model="periodos">
              <option value="1">Primeiro</option>
              <option value="2">Segundo</option>
              <option value="3">Ambos</option>
            </select>
            <div class="input-group-append">
              <label class="input-group-text">Semestre</label>
            </div>
          </div>

          <b-button v-b-modal.modalPerfis title="Perfis" class="cancelbtn">
            <i class="fas fa-list-ul"></i>
          </b-button>
          <b-button v-b-modal.modalCursos title="Cursos" class="cancelbtn">
            <i class="fas fa-graduation-cap"></i>
          </b-button>

          <div class="d-flex p-0 m-0 mt-1">
            <template v-if="isAdd">
              <button
                type="button"
                title="Salvar"
                class="addbtn"
                style="max-width:80px;"
                v-on:click.prevent="addTurma"
              >
                <i class="fas fa-check"></i>
              </button>
              <button
                type="button"
                title="Cancelar"
                class="cancelbtn"
                style="max-width:80px;"
                v-on:click.prevent="toggleAdd"
              >
                <i class="fas fa-times"></i>
              </button>
            </template>
            <template v-else>
              <button
                type="button"
                title="Adicionar"
                class="addbtn"
                style="max-width:80px;"
                v-on:click.prevent="toggleAdd"
              >
                <i class="fas fa-plus"></i>
              </button>
              <button
                type="button"
                title="Deletar"
                class="delbtn"
                style="max-width:80px;"
                v-b-modal.modalConfirma
              >
                <i class="far fa-trash-alt"></i>
              </button>

              <button
                type="button"
                title="XLSX"
                class="relatbtn"
                style="max-width: 65px;"
                v-on:click.prevent="xlsx(Pedidos)"
              >
                <i class="far fa-file-alt"></i>
              </button>
            </template>
            <b-button v-b-modal.modalAjuda title="Ajuda" class="relatbtn">
              <i class="fas fa-question"></i>
            </b-button>
          </div>
        </div>
      </div>
    </div>

    <div class="w-100 mb-2 border-bottom"></div>

    <div id="loading" v-if="isLoading">
      <div class="cube1"></div>
      <div class="cube2"></div>
    </div>

    <div class="pl-0 divTable" v-if="!isLoading" ref="mainTable">
      <table class="table table-bordered table-hover table-sm main-table">
        <thead class="thead-light sticky">
          <tr>
            <turmaheader v-bind:cursos="CursosAtivados"></turmaheader>
          </tr>
        </thead>
        <tbody>
          <template v-if="isAdd">
            <tr style="background-color:#c8c8c8;;">
              <novaturma></novaturma>
            </tr>
          </template>

          <template v-if="Turmas.length>0">
            <template v-for="perfil in PerfisAtivados">
              <tr
                v-for="turma in inPerfil(perfil, Turmas, Disciplinas)"
                :key="'turma id'+turma.id"
                v-bind:style="{backgroundColor: perfil.cor}"
              >
                <template v-if="turma.periodo==1 && (periodos == 1 || periodos==3)">
                  <turmadata
                    ref="turma"
                    v-bind:turma="turma"
                    v-bind:perfil="perfil"
                    v-bind:cursos="CursosAtivados"
                  ></turmadata>
                </template>
              </tr>
            </template>
            <template v-for="perfil in PerfisAtivados">
              <tr
                v-for="turma in inPerfil(perfil, Turmas, Disciplinas)"
                :key="'2-turma id '+turma.id"
                v-bind:style="{backgroundColor: perfil.cor}"
              >
                <template v-if="turma.periodo==3 && (periodos==2 || periodos==3)">
                  <turmadata
                    ref="turma"
                    v-bind:turma="turma"
                    v-bind:perfil="perfil"
                    v-bind:cursos="CursosAtivados"
                  ></turmadata>
                </template>
              </tr>
            </template>
          </template>
        </tbody>
      </table>
    </div>

    <!-- ============ Modals ============== -->
    <!-- Modal de Ajuda -->
    <b-modal id="modalAjuda" ref="ajudaModal" scrollable title="Ajuda">
      <div class="modal-body">
        <ul class="listas list-group">
          <li class="list-group-item">
            <strong>Para exibir conteúdo na Tabela:</strong> Comece selecionando o(s) semestre(s) desejado(s). Em seguida, clique em Perfil e marque quais deseja mostrar, depois clique em OK.
            Logo após, escolha os cursos que quer ver, confirmando a seleção em OK.
          </li>
          <li class="list-group-item">
            <strong>Para adicionar disciplinas à Tabela:</strong> Clique em Adicionar (+), em seguida, preencha a nova linha que surgirá na tabela. Após concluído, clique em Salvar (&#10003;)
  
            ou em Cancelar (X).
          </li>
          <li class="list-group-item">
            <strong>Para editar disciplinas da Tabela: </strong>Faça as alterações necessárias diretamente na tabela e o sistema irá salvar automaticamente. 
          </li>
          <li class="list-group-item">
            <strong>Para deletar disciplinas da Tabela:</strong> Marque a(s) disciplina(s) que deseja deletar através da caixa de seleção à esquerda e em seguida clique em Deletar (&#128465;) e confirme no botão OK.
          </li>
          <li class="list-group-item">
            <strong>Para gerar relatório:</strong> Clique no botão XLSX e aguarde para fazer <font style='font-style:italic;'>download</font> do relatório.
          </li>
        </ul>
      </div>

      <div slot="modal-footer" style="display: none; margin-right: 10px !important;"></div>
    </b-modal>
    <!-- Modals do deletar-->
    <b-modal id="modalConfirma" title="Confirmar Seleção" @ok="deleteSelected">
      <p class="my-4">Tem certeza que deseja deletar as turmas selecionadas?</p>
      <template v-if="Deletar.length > 0">
        <template v-for="turma in Deletar">
          <template v-for="disciplina in Disciplinas">
            <template v-if="disciplina.id===turma.Disciplina">
              <p :key="'disciplina'+disciplina.id+'turma'+turma.id" style="width:80px">
                Disciplina:{{disciplina.codigo}}
                <br />
                Turma:{{turma.letra}}
              </p>
            </template>
          </template>
        </template>
      </template>
    </b-modal>
    <!-- Modals do botão perfis slot="modal-footer" -->
    <b-modal id="modalPerfis" ref="PerfisModal" scrollable title="Selecione os perfis">
      <div
        class="form-group col m-0 p-0 border"
        style="height: 395px; width:max-content; border-color: rgba(0,0,0,0.125);"
      >
        <table class="table table-sm modal-table" style="max-height: 392px !important;">
          <tr>
            <div style="width: max-content; font-size: 11px!important">
              <th class="border-0">
                <p style="width:25px" class="p-header"></p>
              </th>
              <th class="border-0">
                <p class="p-header" style="width: 424px; text-align:start">Nome</p>
              </th>
            </div>
          </tr>
          <tbody>
            <tr v-for="perfil in Perfis" :key="'perfil-id'+perfil.id">
              <div style="width: max-content">
                <td style="padding:0;broder:0;margin:0!important;">
                  <div style="width:25px;">
                    <input
                      type="checkbox"
                      v-model="PerfisSelecionados"
                      :value="perfil"
                      class="form-check-input position-static m-0"
                    />
                  </div>
                </td>
                <td>
                  <p style="width:424px; text-align:start">{{perfil.nome}}</p>
                </td>
              </div>
            </tr>
          </tbody>
        </table>
      </div>
      <!--
      <b-form-group style="margin-top:-10px; font-size:12px;">
        <b-form-checkbox-group id="checkboxGroupPerfis" v-model="PerfisSelecionados">
          <b-form-checkbox
            size="sm"
            v-for="perfil in Perfis"
            :key="'perfil'+perfil.id"
            :value="perfil"
          >{{perfil.nome}}</b-form-checkbox>
        </b-form-checkbox-group>
      </b-form-group>
      -->

      <div slot="modal-footer" style="display: flex; margin-right: 10px !important;">
        <b-button
          class="btn-azul btn-df mr-2"
          variant="success"
          @click="selectAll()"
        >Selecionar Todos</b-button>
        <b-button
          class="btn-cinza btn-df mr-2"
          variant="secondary"
          @click="selectNone()"
        >Desmarcar Todos</b-button>
        <b-button
          variant="success"
          @click="btnOK()"
          class="btn-verde btn-df mr-2"
          style="padding-right:15px!important; padding-left:15px!important;"
        >OK</b-button>
      </div>
    </b-modal>

    <!-- Modals do botão cursos slot="modal-footer" -->
    <b-modal
      id="modalCursos"
      style="max-height:75vh;"
      ref="CursosModal"
      scrollable
      title="Selecione os Cursos"
    >
      <div
        class="form-group m-0 p-0 border"
        style="height: 452px; width:max-content; border-color: rgba(0,0,0,0.125);"
      >
        <table class="modal-table table table-sm" style="max-height: 450px !important;">
          <tr>
            <div style="width: max-content;">
              <th class="border-0">
                <p style="width:25px" class="p-header"></p>
              </th>
              <th
                class="border-0 clickable-header"
                style="text-align:center; font-size:11px;"
                @click="ToggleCodigoOrdering()"
              >
                <p style="width:70px" class="p-header">
                  Código
                  <i
                    v-if="ordenacaoCurso=='codigo'"
                    style="font-size:0.6rem"
                    class="fas fa-arrow-down fa-sm"
                  ></i>
                </p>
              </th>
              <th
                class="border-0 clickable-header"
                style="font-size:11px;"
                @click="ToggleNomeOrdering()"
              >
                <p style="width:340px; text-align: start" class="p-header">
                  Nome
                  <i
                    v-if="ordenacaoCurso=='nome'"
                    style="font-size:0.6rem"
                    class="fas fa-arrow-down fa-sm"
                  ></i>
                </p>
              </th>
            </div>
          </tr>
          <tbody>
            <tr v-for="curso in Cursos" :key="'cursoMd'+curso.id">
              <div style="width: max-content">
                <td style="padding:0;broder:0;margin:0">
                  <div style="width: 25px; margin: 0 auto!important">
                    <b-form-checkbox type="checkbox" v-model="CursosSelecionados" :value="curso"></b-form-checkbox>
                  </div>
                </td>
                <td>
                  <p style="width: 70px">{{curso.codigo}}</p>
                </td>
                <td>
                  <p style="width:340px; text-align: start;">{{curso.nome}}</p>
                </td>
              </div>
            </tr>
          </tbody>
        </table>
      </div>

      <div slot="modal-footer" style="display: flex; margin-right: 10px !important;">
        <b-button
          class="btn-azul btn-df mr-2"
          variant="success"
          @click="selectAllCursos()"
        >Selecionar Todos</b-button>
        <b-button
          class="btn-cinza btn-df mr-2"
          variant="secondary"
          @click="selectNoneCursos()"
        >Desmarcar Todos</b-button>

        <b-button
          variant="success"
          @click="btnOKCursos()"
          class="btn-verde btn-df mr-2"
          style="padding-right:15px!important; padding-left:15px!important"
        >OK</b-button>
      </div>
    </b-modal>
  </div>
</template>

<script>
import _ from "lodash";
import turmaService from "../../common/services/turma";
import pedidoService from "../../common/services/pedido";
import turmadata from "./TurmaRow.vue";
import turmaheader from "./TurmaHeader.vue";
import novaturma from "./NovaTurma.vue";
import xlsx from "../../common/services/xlsx";
import ls from "local-storage";
import { EventBus } from "../../event-bus.js";
import { saveAs } from "file-saver";

const emptyTurma = {
  id: undefined,
  periodo: undefined,
  letra: undefined,
  turno1: undefined,
  turno2: undefined,
  Disciplina: undefined,
  Docente1: undefined,
  Docente2: undefined,
  Horario1: undefined,
  Horario2: undefined,
  Sala1: undefined,
  Sala2: undefined
};

export default {
  name: "DashboardPrototipo",

  data() {
    return {
      turmaForm: _.clone(emptyTurma),
      error: undefined,
      isAdd: false,
      atual: undefined,
      semestre: 1,
      periodos: 3,
      PerfisSelecionados: [],
      CursosSelecionados: [],
      PerfisAtivados: [],
      CursosAtivados: [],
      ordenacaoCurso: "posicao"
    };
  },

  components: {
    turmadata,
    turmaheader,
    novaturma
  },

  mounted: function() {
    ls.set("toggle", -1);
    ls.on("toggle", () => {
      var val = ls.get("toggle");
      if (val === true) {
        this.$store.dispatch("toggleAllCursosTrue");
      } else {
        this.$store.dispatch("toggleAllCursosFalse");
      }
      ls.set("toggle", -1);
    });
    for (var c = 0; c < this.$store.state.curso.Cursos.length; c++) {
      let id = this.$store.state.curso.Cursos[c].id;
      ls.on(`${id}`, () => {
        this.$store.dispatch("toggleCurso", id);
      });
    }
  },

  beforeDestroy: function() {
    ls.off("toggle");
    for (var c = 0; c < this.$store.state.curso.Cursos.length; c++) {
      let id = this.$store.state.curso.Cursos[c].id;
      ls.off(`${id}`);
    }
  },

  methods: {
    ToggleCodigoOrdering() {
      if (this.ordenacaoCurso === "codigo") this.ordenacaoCurso = "posicao";
      else this.ordenacaoCurso = "codigo";
    },

    ToggleNomeOrdering() {
      if (this.ordenacaoCurso === "nome") this.ordenacaoCurso = "posicao";
      else this.ordenacaoCurso = "nome";
    },

    btnOK() {
      //Somente atualiza o vetor de perfis ativados quando o botão OK for clickado
      this.PerfisAtivados = [...this.PerfisSelecionados];
      this.$refs.PerfisModal.hide();
    },
    selectAll() {
      if (this.PerfisSelecionados != []) this.PerfisSelecionados = [];
      for (var i = 0; i < this.$store.state.perfil.Perfis.length; i++)
        this.PerfisSelecionados.push(this.$store.state.perfil.Perfis[i]);
    },

    selectNone() {
      this.PerfisSelecionados = [];
    },

    btnOKCursos() {
      //Somente atualiza o vetor de perfis ativados quando o botão OK for clickado
      this.CursosAtivados = [...this.CursosSelecionados];
      this.CursosAtivados = _.orderBy(this.CursosAtivados, this.ordenacaoCurso);
      this.$refs.CursosModal.hide();
    },
    selectAllCursos() {
      if (this.CursosSelecionados != []) this.CursosSelecionados = [];
      for (var i = 0; i < this.$store.state.curso.Cursos.length; i++)
        this.CursosSelecionados.push(this.$store.state.curso.Cursos[i]);
    },

    selectNoneCursos() {
      this.CursosSelecionados = [];
    },

    xlsx: function(pedidos) {
      xlsx
        .downloadTable({ pedidos: pedidos })
        .then(() => {
          console.log("done");
          fetch("http://200.131.219.57:3000/api/xlsx/download", {
            method: "GET",
            headers: { Authorization: `Bearer ${this.$store.state.auth.token}` }
          })
            .then(r => r.blob())
            .then(blob => saveAs(blob, "tabela.xlsx"));
        })
        .catch(error => console.log(error));
    },

    adjustTurno1: function() {
      if (
        this.turmaForm.Horario1 == 1 ||
        this.turmaForm.Horario1 == 2 ||
        this.turmaForm.Horario1 == 7 ||
        this.turmaForm.Horario1 == 8 ||
        this.turmaForm.Horario1 == 13 ||
        this.turmaForm.Horario1 == 14 ||
        this.turmaForm.Horario1 == 19 ||
        this.turmaForm.Horario1 == 20 ||
        this.turmaForm.Horario1 == 25 ||
        this.turmaForm.Horario1 == 26 ||
        this.turmaForm.Horario1 == 3 ||
        this.turmaForm.Horario1 == 4 ||
        this.turmaForm.Horario1 == 9 ||
        this.turmaForm.Horario1 == 10 ||
        this.turmaForm.Horario1 == 15 ||
        this.turmaForm.Horario1 == 16 ||
        this.turmaForm.Horario1 == 21 ||
        this.turmaForm.Horario1 == 22 ||
        this.turmaForm.Horario1 == 27 ||
        this.turmaForm.Horario1 == 28
      ) {
        this.turmaForm.turno1 = "Diurno";
      } else if (this.turmaForm.Horario1 == 31) {
        this.turmaForm.turno1 = "EAD";
      } else {
        this.turmaForm.turno1 = "Noturno";
      }
    },

    adjustTurno2: function() {
      if (
        this.turmaForm.Horario2 == 1 ||
        this.turmaForm.Horario2 == 2 ||
        this.turmaForm.Horario2 == 7 ||
        this.turmaForm.Horario2 == 8 ||
        this.turmaForm.Horario2 == 13 ||
        this.turmaForm.Horario2 == 14 ||
        this.turmaForm.Horario2 == 19 ||
        this.turmaForm.Horario2 == 20 ||
        this.turmaForm.Horario2 == 25 ||
        this.turmaForm.Horario2 == 26 ||
        this.turmaForm.Horario2 == 3 ||
        this.turmaForm.Horario2 == 4 ||
        this.turmaForm.Horario2 == 9 ||
        this.turmaForm.Horario2 == 10 ||
        this.turmaForm.Horario2 == 15 ||
        this.turmaForm.Horario2 == 16 ||
        this.turmaForm.Horario2 == 21 ||
        this.turmaForm.Horario2 == 22 ||
        this.turmaForm.Horario2 == 27 ||
        this.turmaForm.Horario2 == 28
      ) {
        this.turmaForm.turno1 = "Diurno";
      } else if (this.turmaForm.Horario2 == 31) {
        this.turmaForm.turno1 = "EAD";
      } else {
        this.turmaForm.turno1 = "Noturno";
      }
    },

    deleteSelected: function() {
      var turmas = this.$store.state.turma.Deletar;
      for (var i = 0; i < turmas.length; i++) {
        this.deleteTurma(turmas[i]);
      }
      this.$store.commit("emptyDelete");
    },

    inPerfil: function(perfil, turmas, disciplinas) {
      return turmas.filter(function(turma) {
        if (_.isNull(turma.Disciplina)) return false;
        var disciplina = _.find(disciplinas, function(disc) {
          return disc.id === turma.Disciplina;
        });
        return disciplina.Perfil === perfil.id;
      });
    },

    addTurma() {
      EventBus.$emit("addTurma");
    },

    editTurma(turma) {
      turmaService
        .update(turma.id, turma)
        .then(response => {
          this.$notify({
            group: "general",
            title: `Sucesso!`,
            text: `A Turma ${response.Turma.letra} foi atualizada!`,
            type: "success"
          });
        })
        .catch(error => {
          this.error = "<b>Erro ao atualizar Turma</b>";
          if (error.response.data.fullMessage) {
            this.error +=
              "<br/>" + error.response.data.fullMessage.replace("\n", "<br/>");
          }
        });
    },

    deleteTurma(turma) {
      turma.periodo = null;
      turma.letra = null;
      turma.turno1 = null;
      turma.turno2 = null;
      turma.Disciplina = null;
      turma.Docente1 = null;
      turma.Docente2 = null;
      turma.Horario1 = null;
      turma.Horario2 = null;
      turma.Sala1 = null;
      turma.Sala2 = null;
      console.log(turma);

      this.editTurma(turma);

      var pedidos = this.$store.state.pedido.Pedidos[turma.id];
      for (var i = 0; i < pedidos.length; i++) {
        if (
          !(
            pedidos[i].vagasPeriodizadas === 0 &&
            pedidos[i].vagasNaoPeriodizadas === 0
          )
        ) {
          pedidos[i].vagasPeriodizadas = 0;
          pedidos[i].vagasNaoPeriodizadas = 0;
          pedidoService
            .update(pedidos[i].Curso, pedidos[i].Turma, pedidos[i])
            .then(() => {
              this.$notify({
                group: "general",
                title: `Sucesso!`,
                text: `O pedido foi atualizado!`,
                type: "success"
              });
            })
            .catch(error => {
              this.error = "<b>Erro ao atualizar Pedido</b>";
              if (error.response.data.fullMessage) {
                this.error +=
                  "<br/>" +
                  error.response.data.fullMessage.replace("\n", "<br/>");
              }
            });
        }
      }
    },

    toggleAdd() {
      this.isAdd = !this.isAdd;
    }
  },

  computed: {
    Cursos() {
      return _.orderBy(this.$store.state.curso.Cursos, this.ordenacaoCurso);
    },

    CursosAtivos() {
      return this.$store.state.curso.Ativos;
    },

    Disciplinas() {
      return _.orderBy(this.$store.state.disciplina.Disciplinas, "nome");
    },

    Docentes() {
      return _.orderBy(
        _.filter(this.$store.state.docente.Docentes, ["ativo", true]),
        "apelido"
      );
    },

    Horarios() {
      return _.orderBy(this.$store.state.horario.Horarios, "horario");
    },

    Salas() {
      return _.orderBy(this.$store.state.sala.Salas, "nome");
    },

    Perfis() {
      return this.$store.state.perfil.Perfis;
    },

    Turmas() {
      return _.orderBy(
        _.orderBy(this.$store.state.turma.Turmas, "letra"),
        "Disciplina"
      );
    },

    Deletar() {
      return this.$store.state.turma.Deletar;
    },

    Pedidos() {
      return this.$store.state.pedido.Pedidos;
    },

    isLoading() {
      return this.$store.state.isLoading;
    },

    Admin() {
      if (this.$store.state.auth.Usuario.admin === 1) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>

<style scoped>
/* prefixed by https://autoprefixer.github.io (PostCSS: v7.0.23, autoprefixer: v9.7.3) */
.listas {
  line-height: 30px;
  font-size: 12px;
  text-align: justify;
  line-height: inherit;
  box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.15);
}
strong {
  color: #007bff;
}
.DashboardPrototipo {
