<template>
    <div class="DashboardRelatorioDisciplinas row pr-2">
        <!-- Titulo -->
        <div
      class="div-titulo col-12 d-flex center-content-between flex-wrap flex-md-nowrap p-0 mb-0"
      style="height:38px;"
    >
      <div class="form-inline col-12 pl-0 mb-1 pr-1">
        <h1 class="titulo col-9 col-sm-6 col-md-4 col-lg-4 px-0 pr-1">Plano Departamental</h1>

        <div
          class="form-group col-3 col-sm-6 col-md-8 col-lg-8 mb-0 p-0"
          style="justify-content: flex-end!important;"
        >
        
            <div class="input-group ml-auto mb-0 mt-0 p-0">
                <b-button v-b-modal.modalDisciplinas title="Disciplinas" class="cancelbtn">
                    <i class="fas fa-list-ul"></i>
                </b-button>
                <button type="button" class="relatbtn" title="Relatório" v-on:click.prevent="pdf">
                    <i class="far fa-file-alt"></i>
                </button>

                <b-button v-b-modal.modalAjuda title="Ajuda" class="relatbtn">
                    <i class="fas fa-question"></i>
                </b-button>
              </div>
            </div>
        </div>
      </div>
    

    <div class="w-100 mb-2 border-bottom"></div>
        
        <div class="divTable p-0" ref="carga">
            <table class="table table-hover border table-sm">
                <thead class="thead-light sticky">
                <tr>
                    <div
                            style="display: block; overflow: hidden; width: ‭845‬px; height:20px !important"
                            class="sticky"
                    >
                        <th scope="col">
                            <p class="p-header clickable-header" style="width: 80px;"
                               @click="toggleOrderCodigo()"
                               title="Clique para ordenar por código">Cod.
                                <i
                                        v-if="ordenacao=='codigo'"
                                        style="font-size:0.6rem; text-align:right"
                                        class="fas fa-arrow-down fa-sm"
                                ></i>
                            </p>
                        </th>
                        <th scope="col">
                            <p class="p-header clickable-header" style="width: 350px"
                               @click="toggleOrderNome()"
                               title="Clique para ordenar por nome">Nome
                                <i
                                    v-if="ordenacao=='nome'"
                                    style="font-size:0.6rem; text-align:right"
                                    class="fas fa-arrow-down fa-sm"
                                ></i>
                            </p>
                        </th>
                        <th scope="col">
                            <p class="p-header" style="width: 24px">S.</p>
                        </th>
                        <th scope="col">
                            <p class="p-header" style="width: 24px">T.</p>
                        </th>
                        <th scope="col">
                            <p class="p-header" style="width: 200px">Docentes</p>
                        </th>
                        <th scope="col">
                            <p class="p-header" style="width: 180px">Horário</p>
                        </th>
                    </div>
                </tr>
                </thead>
                <tbody>
                <template v-if="Disciplinas.length > 0">
                    <template v-for="disciplina in DisciplinasAtivados">
                        <template v-if="turmas(disciplina).length > 0">
                            <div class="linhas" style="width: ‭845‬px;" :key="disciplina.codigo">
                                <td class="disc-td">
                                    <div style="width: 80px">{{disciplina.codigo}}</div>
                                </td>
                                <td class="disc-td">
                                    <div style="width: 350px;">{{disciplina.nome}}</div>
                                </td>
                                <td class="disc-td">
                                    <div style="width: 428px; height: 20px;"></div>
                                </td>

                            </div>
                        </template>

                        <template v-for="turma in turmas(disciplina)">
                            <tr>
                                <template>
                                    <div class="linhas" style="width: ‭858px;">
                                        <td>
                                            <p style="width: 80px"></p>
                                        </td>
                                        <td>
                                            <p style="width: 350px"></p>
                                        </td>
                                        <td>
                                            <p style="width: 24px">{{turma.periodo}}</p>
                                        </td>
                                        <td>
                                            <p style="width: 24px">{{turma.letra}}</p>
                                        </td>
                                        <td>
                                            <p style="width: 200px;">{{docentes(turma)}}</p>
                                        </td>

                                        <td>
                                            <div style="width: 180px">
                                                <template v-for="horario in Horarios">
                                                    <p :key="horario.id" v-if="horario.id===turma.Horario1">
                                                        {{horario.horario}}
                                                        <template v-for="horario in Horarios">
                                <span
                                        :key="horario.id"
                                        v-if="horario.id===turma.Horario2"
                                >/ {{horario.horario}}</span>
                                                        </template>
                                                    </p>
                                                </template>
                                            </div>
                                        </td>
                                    </div>
                                </template>
                            </tr>
                        </template>

                    </template>
                </template>
                <template v-else>
                    <tr>
                        <td colspan="2" class="text-center">
                            <i class="fas fa-exclamation-triangle"></i> Nenhuma Disciplina encontrado!
                        </td>
                    </tr>
                </template>
                </tbody>
            </table>
        </div>
        
        <!-- MODAL DE AJUDA -->
    <b-modal id="modalAjuda" ref="ajudaModal" scrollable title="Ajuda">
      
      <div class="modal-body">
        <ul class="listas list-group"> 
          <li class="list-group-item">
            <strong>Para exibir conteúdo na Tabela: </strong> Clique em Disciplinas e selecione a(s) disciplina(s) que deseja 
            ver, em seguida confirme em OK. Caso queira ver todas basta clicar em Selecionar Todos.
          </li>
          <li class="list-group-item">
            <strong>Para gerar relatório:</strong> Clique no botão Relatório e aguarde para fazer <font style='font-style:italic;'>download</font> do mesmo.
          </li>
          <li class="list-group-item">
            <strong>Para alterar ordenação:</strong> Clique em Cod. no cabeçalho da tabela para ordenar por código, ou em Nome para ordenar por ordem alfabética. 
          </li>
        </ul>
      </div>

      <div slot="modal-footer" style="display: none">
      </div>
    </b-modal>
        <!-- Modals do botão para escolher disciplinas -->
        <b-modal id="modalDisciplinas" ref="DisciplinasModal" scrollable title="Selecione as disciplinas">
            <div
                    class="form-group col m-0 p-0 border"
                    style="height: 395px; width:max-content; border-color: rgba(0,0,0,0.125);"
            >
                <table class="table table-sm modal-table" style="max-height: 392px !important;">
                    <tr>
                        <div style="width: max-content; font-size: 11px!important">
                            <th class="border-0">
                                <p style="width:25px" class="p-header"></p>
                            </th>
                            <th class="border-0">
                                <p class="p-header" style="width: 80px; text-align:start">Cod.</p>
                            </th>
                            <th class="border-0">
                                <p class="p-header" style="width: 424px; text-align:start">Nome</p>
                            </th>
                        </div>
                    </tr>
                    <tbody>
                    <tr v-for="disciplina in Disciplinas" :key="`disciplina${disciplina.id}`">
                        <div style="width: max-content">
                            <td style="padding:0;broder:0;margin:0!important;">
                                <div style="width:25px;">
                                    <input
                                            type="checkbox"
                                            v-model="DisciplinasSelecionados"
                                            :value="disciplina"
                                            class="form-check-input position-static m-0"
                                    />
                                </div>
                            </td>
                            <td>
                                <p style="width:80px; text-align:start">{{disciplina.codigo}}</p>
                            </td>
                            <td>
                                <p style="width:424px; text-align:start">{{disciplina.nome}}</p>
                            </td>
                        </div>
                    </tr>
                    </tbody>
                </table>
            </div>

            <div slot="modal-footer" style="display: flex; margin-right: 10px !important;">
                <b-button
                        class="btn-azul btn-df mr-2"
                        variant="success"
                        @click="selectAll()"
                >Selecionar Todos</b-button>
                <b-button
                        class="btn-cinza btn-df mr-2"
                        variant="secondary"
                        @click="selectNone()"
                >Desmarcar Todos</b-button>
                <b-button
                        variant="success"
                        @click="btnOK()"
                        class="btn-verde btn-df mr-2"
                        style="padding-right:15px!important; padding-left:15px!important;"
                >OK</b-button>
            </div>
        </b-modal>
    </div>
</template>

<script>
    import _ from "lodash";
    import pdfs from "../../common/services/pdfs";
    export default {
        name: "DashboardRelatorioDisciplinas",

        data() {
            return {
                ordenacao:"codigo",
                DisciplinasSelecionados: [],
                DisciplinasAtivados: []
            }
        },

        methods: {
            pdf() {
                pdfs.pdfRelatorioDisciplinas()
            },

            btnOK() {
                //Somente atualiza o vetor de perfis ativados quando o botão OK for clickado
                this.DisciplinasAtivados = [..._.orderBy(this.DisciplinasSelecionados, 'codigo')];
                this.$refs.DisciplinasModal.hide();
            },

            selectAll() {
                if (this.DisciplinasSelecionados != []) this.DisciplinasSelecionados = [];
                for (var i = 0; i < this.$store.state.disciplina.Disciplinas.length; i++)
                    this.DisciplinasSelecionados.push(this.$store.state.disciplina.Disciplinas[i]);
            },

            selectNone() {
                this.DisciplinasSelecionados = [];
            },

            toggleOrderNome() {
                this.DisciplinasAtivados = _.orderBy(this.DisciplinasAtivados, 'nome')
                this.ordenacao = "nome";
            },
            toggleOrderCodigo() {
                this.DisciplinasAtivados = _.orderBy(this.DisciplinasAtivados, 'codigo')
                this.ordenacao = "codigo";
            },

            turmas(disciplina) {
                return _.orderBy(
                    _.filter(this.$store.state.turma.Turmas, turma => {
                        return (
                            turma.Disciplina === disciplina.id
                        );
                    }),
                    ['periodo', 'letra']
                );
            },

            docentes(turma) {
                let d1 = _.find(this.$store.state.docente.Docentes, {'id':turma.Docente1})
                let d2 = _.find(this.$store.state.docente.Docentes, {'id':turma.Docente2})
                if(d1 === undefined && d2 === undefined){
                    return ''
                }else if(d2 === undefined){
                    return `${d1.apelido}`
                }else if(d1 === undefined){
                    return `${d2.apelido}`
                }else {
                    return `${d1.apelido} / ${d2.apelido}`
                }
            }
        },

        computed: {
            Disciplinas() {
                return _.orderBy(_.filter(this.$store.state.disciplina.Disciplinas, function(d) {
                    return d.Perfil !== 13 && d.Perfil !== 15;
                }), this.ordenacao);
            },
            Horarios() {
                return this.$store.state.horario.Horarios;
            },

        }
    };
</script>

