        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
    }
    table {
        display: block;
        overflow-y: scroll;
        height: -webkit-calc(100vh - 100px);
        height: -moz-calc(100vh - 100px);
        height: calc(100vh - 100px);
        font-size: 11px;
        background-color: white;
