        -moz-box-shadow: 0 0 0 0.2rem rgba(108, 166, 127, 0.5) !important;
        box-shadow: 0 0 0 0.2rem rgba(108, 166, 127, 0.5) !important;
    }

    i.fas,
    i.far {
